import React, {Component} from 'react';
import { Widget, addResponseMessage, addUserMessage, dropMessages } from 'react-chat-widget';
import 'react-chat-widget/lib/styles.css';

class Client extends Component {
  componentDidMount() {
    addResponseMessage('Welcome to T2Codes.com!');
    addResponseMessage('How may I help you?');
  }

  render() {
    return (
      <div className='App'>
        <Widget
          handleNewUserMessage={this.handleNewUserMessage}
          title='T2Codes customer service'
          subtitle='Chat online with our experts'
          
        />
      </div>
    );
  }

  handleNewUserMessage = newMessage => {
    if(newMessage.toLowerCase() in ["hello", "hi", "good morning!"] === true)
        addResponseMessage("Hello!");
    addResponseMessage("Sorry, I don't understand");
    return;
  }

  componentWillUnmount() {
    dropMessages();
  }
}

export default Client;