import { Modal, CardBody, Label, Button } from 'reactstrap';
import { Control, LocalForm, Errors } from 'react-redux-form';
import { login } from '../../store/auth/actions';
import { uiActions } from '../../store/ui/slice';
import { useDispatch, useSelector } from 'react-redux';

const required = (val) => val && val.length;
// const maxLength = (len) => (val) => !(val) || (val.length <= len);
// const minLength = (len) => (val) => val && (val.length >= len);
// const isNumber = (val) => !isNaN(Number(val));
// const validEmail = (val) => /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);

export default function(){
    const toggle = useSelector((state) => state.ui.loginModal);
    const dispatch = useDispatch();
    const handleSubmit = (values) => {
        dispatch(uiActions.toggleLoginModal());
        const creds = JSON.stringify(values);
        dispatch(login(creds));
    }

    return(
            <Modal className="card border-0 shadow rounded-3 my-5" isOpen={toggle} toggle={() => dispatch(uiActions.toggleLoginModal())}>
            <CardBody className="card-body p-4 p-sm-5">
                <h5 className="card-title text-center mb-5 fw-light fs-5">Login to your account</h5>
                <LocalForm onSubmit={(values) => handleSubmit(values)}>
                <div className="form-floating mb-3">
                    <Control.text 
                        model=".username"
                        className="form-control" 
                        id="floatingInput" 
                        validators={{
                            required
                        }}
                        />
                    <Label htmlFor="floatingInput">Username</Label>
                    <Errors
                        className="text-danger"
                        model=".username"
                        show="touched"
                        messages={{
                            required: 'Required'
                        }}
                    />
                </div>
                <div className="form-floating mb-3">
                    <Control.password 
                        model=".password" 
                        className="form-control" 
                        id="floatingPassword" 
                        placeholder="Password"
                        validators={{
                            required
                        }}
                    />
                    <Label htmlFor="floatingPassword">Password</Label>
                    <Errors
                        className="text-danger"
                        model=".username"
                        show="touched"
                        messages={{
                            required: 'Required',
                        }}
                    />
                    
                </div>

                <div className="form-check mb-3">
                    <Control.checkbox model=".rememberPassword" className="form-check-input" id="rememberPassword"/>
                    <Label className="form-check-label" htmlFor="rememberPassword">
                    Remember password
                    </Label>
                </div>
                <div className="d-grid">
                    <Button className="fw-bold" color='primary' type="submit">Login</Button>
                </div>
                </LocalForm>
            </CardBody>
            </Modal>
    )
}
