import { createSlice } from "@reduxjs/toolkit";

const uiSlice = createSlice({
  name: "ui",
  initialState: { 
    notification: null,
    isOpen: false,
    createPollModal: false,
    createPollType: 'polls',
    numberOfChoices: 2,
    registerModal: false,
    loginModal: false,
    supportModal: false
    },
  reducers: {
    showNotification(state, action) {
      state.notification = {
        message: action.payload.message,
        type: action.payload.type,
        open: action.payload.open,
      };
    },
    toggle(state) {
        state.isOpen = !state.isOpen;
    },
    toggleCreatePollModal(state) {
      state.createPollModal = !state.createPollModal;
    },
    toggleRegisterModal(state) {
      state.registerModal = !state.registerModal;
    },
    toggleLoginModal(state) {
      state.loginModal = !state.loginModal;
    },
    toggleSupportModal(state) {
      state.supportModal = !state.supportModal;
    },
    changeCreateFormType(state, action){
      state.createPollType = action.payload;
    },
    changeNumberOfChoices(state, action){
      state.numberOfChoices = action.payload;
    }
  },
});

export const uiActions = uiSlice.actions;

export default uiSlice;