import logo from '../images/logo.png';
import React from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem, 
  Button} from 'reactstrap';

import { Link } from "react-router-dom";
import { useSelector, useDispatch } from 'react-redux'
import { uiActions } from '../store/ui/slice'
import { authActions } from '../store/auth/slice'


export default function() {
  const isOpen = useSelector((state) => state.ui.isOpen);
  const isLoggedIn = useSelector((state) => state.auth.isLoggedIn);
  const username = useSelector((state) => state.auth.username);
  const dispatch = useDispatch();
  return (
    <div>
    <Navbar color="dark" className="navbar navbar-expand-lg">
          <NavbarBrand>
            <img src={logo} className="logo"></img>
          </NavbarBrand>
          <NavbarToggler onClick={() => dispatch(uiActions.toggle())} />
          <Collapse isOpen={isOpen} navbar>
          <Nav className="links" navbar>
              <NavItem>
              <Link to="home" className='nav-link text-white'>Home</Link>
              </NavItem>
              <NavItem>
                  <Link to="support" className='nav-link text-white'>Support</Link>
              </NavItem>
              {!isLoggedIn && <NavItem>
                <p onClick={() => dispatch(uiActions.toggleLoginModal())} className='nav-link text-white'>Login</p>
              </NavItem>}
              
              {!isLoggedIn && <NavItem>
                <p onClick={() => dispatch(uiActions.toggleRegisterModal())} className='btn btn-primary'>Register</p>
              </NavItem>}

              {isLoggedIn && <NavItem>
                  <Button onClick={() => dispatch(authActions.logout())} color="primary">Logout {username}</Button>
              </NavItem>}
              
          </Nav>
          </Collapse>
      </Navbar>
    </div>
  )
}
