export default function(){
    return(
        <div className="container">
        <div class="row">
        <div class="col-sm-9 col-md-7 col-lg-5 mx-auto">
            <div class="card border-0 shadow rounded-3 my-5">
            <div class="card-body p-4 p-sm-5">
                <h5 class="card-title text-center mb-5 fw-light fs-5">Send us a message</h5>
                <form>
                <div class="form-floating mb-3">
                    <input type="text" class="form-control" id="floatingInput" placeholder="Name"/>
                    <label for="floatingInput">Name</label>
                </div>
                <div class="form-floating mb-3">
                    <input type="email" class="form-control" id="floatingInput" placeholder="name@example.com"/>
                    <label for="floatingInput">Email address</label>
                </div>
                <div class="form-group mb-3">
                    <textarea class="form-control" id="floatingInput" rows="10" placeholder="Your message"></textarea>
                </div>
                <div class="d-grid">
                    <button class="btn btn-primary btn-login fw-bold" type="submit">Send</button>
                </div>
                </form>
            </div>
            </div>
        </div>
        </div>
        </div>
    )
}