import { Modal, Label, CardBody, Button } from 'reactstrap';
import { Control, LocalForm, Errors } from 'react-redux-form';
import { register } from '../../store/auth/actions';
import { uiActions } from '../../store/ui/slice';
import { useDispatch, useSelector } from 'react-redux';

const required = (val) => val && val.length;
const validEmail = (val) => /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);

export default function(){
    const toggle = useSelector((state) => state.ui.registerModal);
    const dispatch = useDispatch();
    const handleSubmit = (values) => {
        dispatch(uiActions.toggleRegisterModal());
        const data = JSON.stringify(values);
        dispatch(register(data));
    }
    return(
        <Modal className="card border-0 shadow rounded-3 my-5" isOpen={toggle} toggle={() => dispatch(uiActions.toggleRegisterModal())}>
            <CardBody className="p-4 p-sm-5">
                <h5 className="card-title text-center mb-5 fw-light fs-5">Create an account</h5>
                <LocalForm onSubmit={handleSubmit}>
                <div className="form-floating mb-3">
                    <Control.text 
                        model=".username"
                        className="form-control" 
                        id="username" 
                        validators={{
                            required
                        }}
                        />
                    <Label htmlFor="username">Username</Label>
                    <Errors
                        className="text-danger"
                        model=".username"
                        show="touched"
                        messages={{
                            required: 'Required'
                        }}
                    />
                </div>
                <div className="form-floating mb-3">
                <Control.text 
                        model=".email"
                        className="form-control" 
                        id="email" 
                        validators={{
                            required,
                            validEmail
                        }}
                        />
                    <Label htmlFor="email">Email</Label>
                    <Errors
                        className="text-danger"
                        model=".email"
                        show="touched"
                        messages={{
                            required: 'Required',
                            validEmail: 'Invalid email'
                        }}
                    />
                </div>
                <div className="form-floating mb-3">
                    <Control.password 
                        model=".password" 
                        className="form-control" 
                        id="password" 
                        placeholder="Password"
                        validators={{
                            required
                        }}
                    />
                    <Label htmlFor="password">Password</Label>
                    <Errors
                        className="text-danger"
                        model=".password"
                        show="touched"
                        messages={{
                            required: 'Required',
                        }}
                    />
                </div>
                <div className="form-floating mb-3">
                    <Control.password 
                        model=".password2" 
                        className="form-control" 
                        id="password2" 
                        placeholder="Confirm password"
                        validators={{
                            required
                        }}
                    />
                    <Label htmlFor="password2">Password</Label>
                    <Errors
                        className="text-danger"
                        model=".password2"
                        show="touched"
                        messages={{
                            required: 'Required',
                        }}
                    />
                </div>

                <div className="form-check mb-3">
                    <Control.checkbox model=".agreeTermsAndServices" className="form-check-input" id="agreeTermsAndServices"/>
                    <Label className="form-check-label" htmlFor="agreeTermsAndServices">
                    I agree all statements in <a href="#!">Terms of service</a>
                    </Label>
                </div>
                <div className="d-grid">
                    <Button className="fw-bold" color='primary' type="submit">Register</Button>
                </div>
                </LocalForm>
            </CardBody>
        </Modal>
    )
}