import {Link, Route, Routes} from 'react-router-dom';
import { Container } from 'reactstrap';
import Home from './Home';
import Register from './auth/Register';
import Login from './auth/Login';
import Support from './Support';
import Notification from './Notification';
import Footer from './Footer';
import Navbar from './Navbar';
import CookieConsent from "react-cookie-consent";
import { useSelector, useDispatch } from 'react-redux';

export default function(){
    const notification = useSelector((state) => state.ui.notification);
    return(
    <div className='Main'>
      <Container>
      <CookieConsent
      enableDeclineButton
      onDecline={() => {
        alert("nay!");
      }}
      location="bottom"
      buttonText="Accept"
      cookieName="myAwesomeCookieName2"
      declineButtonClasses="btn btn-danger rounded"
      buttonClasses='btn btn-primary bg-primary text-white rounded'
      style={{ background: "black", color: "blue", fontSize: "22px" }}
      expires={150}
    >
      <p className='text h5 text-white'>This website uses cookies to enhance the user experience.</p>
      </CookieConsent>
      <Navbar/>
      {notification && (
        <Notification type={notification.type} message={notification.message} />
      )}

      <Register/>
      <Login/>
      <Routes>
            <Route path='home' element={<Home/>}/>
            <Route path='support' element={<Support/>}/>
            <Route path='*' element={<Home/>}/>
        </Routes>
      
      </Container>

      <Footer/>
    </div>
    )
}